@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --background: #121212;
  --surface: #1e1e1e;
  --text-primary: #ffffff;
  --text-secondary: #b0b0b0;
  --jason-electric: #00a3ff;
  --jason-sapphire: #0066ff;
  --jason-amber: #ffb800;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background);
  color: var(--text-primary);
}

.bg-background {
  background-color: var(--background);
}

.bg-surface {
  background-color: var(--surface);
}

.text-text-primary {
  color: var(--text-primary);
}

.text-jason-electric {
  color: var(--jason-electric);
}

.bg-jason-electric {
  background-color: var(--jason-electric);
}

.from-jason-sapphire {
  --tw-gradient-from: var(--jason-sapphire);
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to, rgba(0, 102, 255, 0));
}

.to-jason-electric {
  --tw-gradient-to: var(--jason-electric);
}

.text-jason-amber {
  color: var(--jason-amber);
}

/* Animation classes */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.animate-fadeIn {
  animation: fadeIn 0.5s ease-out forwards;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.animate-pulse-custom {
  animation: pulse 2s infinite;
}

/* Device sharing animation */
@keyframes deviceShare {
  0% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.5); opacity: 0.5; }
  100% { transform: scale(0); opacity: 0; }
}

.animate-device-share {
  animation: deviceShare 1s forwards;
}